<template>
  <div class="add-container">
    <el-card class="box-card">
      <el-alert title="添加商品信息" type="info" center show-icon> </el-alert>
      <el-steps :active="activeName - 0">
        <el-step title="基本信息"></el-step>
        <el-step title="商品参数"></el-step>
        <el-step title="商品属性"></el-step>
        <el-step title="商品图片"></el-step>
        <el-step title="商品内容"></el-step>
        <el-step title="完成"></el-step>
      </el-steps>
      <!-- tab栏 -->
      <el-tabs
        :tab-position="tabPosition"
        style="height: 200px"
        :before-leave="beforeleave"
        v-model="activeName"
      >
        <!-- 基本信息 -->
        <el-tab-pane label="基本信息" name="0">
          <el-form :model="form" :rules="rules" ref="form">
            <el-form-item
              label="商品名称"
              :label-width="formLabelWidth"
              prop="goods_name"
            >
              <el-input v-model="form.goods_name" autocomplete="off"></el-input>
            </el-form-item>

            <el-form-item
              label="商品价格"
              prop="goods_price"
              :label-width="formLabelWidth"
            >
              <el-input
                v-model="form.goods_price"
                autocomplete="off"
              ></el-input>
            </el-form-item>

            <el-form-item
              label="商品重量"
              :label-width="formLabelWidth"
              prop="goods_weight"
            >
              <el-input
                v-model="form.goods_weight"
                autocomplete="off"
              ></el-input>
            </el-form-item>

            <el-form-item
              label="商品数量"
              :label-width="formLabelWidth"
              prop="goods_number"
            >
              <el-input
                v-model="form.goods_number"
                autocomplete="off"
              ></el-input>
            </el-form-item>

            <el-form-item
              label="商品分类"
              :label-width="formLabelWidth"
              prop="mobile"
            >
            </el-form-item>
            <!-- 级联选择器 -->
            <el-form-item label="">
              <el-cascader :options="options" v-model="value" :props="props">
              </el-cascader> </el-form-item
            >==========={{ value }}=========={{ flag }}
          </el-form>
        </el-tab-pane>
        <!-- 商品参数 -->
        <el-tab-pane label="商品参数" name="1">
          <el-button
            type="success"
            v-for="item in onlyarr"
            :key="item.attr_id"
            >{{ item.attr_value }}</el-button
          >
        </el-tab-pane>
        <!-- 商品属性 -->
        <el-tab-pane label="商品属性" name="2">
          <ul>
            <li v-for="(item, index) in manyarr" :key="index">
              <h4 style="margin-bottom: 20px">
                {{ item.attr_id }}
              </h4>

              <div
                style="margin-bottom: 20px"
                v-for="(item1, index) in item.attr_value.split(',')"
                :key="index"
              >
                {{ item1 }}
              </div>
            </li>
          </ul>
        </el-tab-pane>

        <el-tab-pane label="商品图片" name="3">
          <el-upload
            class="upload-demo"
            action="http://192.168.68.23:8080/api/private/v1/upload"
            :on-success="success"
            :file-list="fileList"
            list-type="picture"
            :headers="headers"
          >
            <el-button size="small" type="primary">点击上传</el-button>
            <div slot="tip" class="el-upload__tip"></div>
          </el-upload>
        </el-tab-pane>
        <el-tab-pane label="商品内容" name="4"
          ><quillEditor></quillEditor>
          <el-button type="primary" class="addroles" @click="addgoods"
            >添加商品</el-button
          >
        </el-tab-pane>
      </el-tabs>
    </el-card>
  </div>
</template>

<script>
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
import { quillEditor } from 'vue-quill-editor'
export default {
  components: {
    quillEditor
  },
  name: '',
  data () {
    return {
      disabled: false,
      tabPosition: 'left',
      activeName: '0',
      fileList: [],
      headers: {
        Authorization: localStorage.getItem('token')
      },
      form: {
        goods_name: '',
        goods_price: '',
        goods_number: '',
        goods_weight: '',
        goods_cat: '',
        goods_introduce: '',
        attrs: [],
        pics: []
      },
      onlyarr: [],
      manyarr: [],
      formLabelWidth: '100px',
      options: [],
      value: [],
      props: {
        label: 'cat_name',
        children: 'children',
        value: 'cat_id'
      },
      // 规则
      rules: {
        goods_name: [
          { required: true, message: '请输入商品名称', trigger: 'blur' },
          { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '请输入价格', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '请输入价格', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '请输入价格', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.getfenleidata()
  },
  methods: {
    async beforeleave (activeName, oldActiveName) {
      if (!this.flag) {
        this.$message({
          showClose: true,
          message: '请选择商品分类',
          type: 'error'
        })
        return new Promise()
      }
      // 商品参数
      if (activeName == 1) {
        var id = this.value[this.value.length - 1]
        const res = await this.$http.get(`categories/${id}/attributes`, {
          params: {
            sel: 'only'
          }
        })
        const arr = res.data.data.map(item => {
          return { attr_id: item.attr_id, attr_value: item.attr_vals }
        })
        this.onlyarr = arr
      }
      // 商品属性
      if (activeName == 2) {
        var id = this.value[this.value.length - 1]
        const res = await this.$http.get(`categories/${id}/attributes`, {
          params: {
            sel: 'many'
          }
        })
        // console.log(res, 777777)
        const arr2 = res.data.data.map(item => {
          return {
            attr_id: item.attr_id,
            attr_value: item.attr_vals
          }
        })
        this.manyarr = arr2
      }
    },
    // 图片上传
    success (response, file, fileList) {
      if (response.meta.status == 200) {
        this.$message({
          message: '图片上传成功',
          type: 'success'
        })
        this.form.pics.push({ pic: response.data.tmp_path })
      }
    },
    async getfenleidata () {
      const {
        data: { data }
      } = await this.$http.get('categories', { params: { type: 3 } })
      this.options = data
    },
    // 添加商品
    async addgoods () {
      this.form.goods_cat = this.value.join()
      this.form.attrs = [...this.onlyarr, ...this.manyarr]
      const res = await this.$http.post('goods', this.form)
    }
  },
  computed: {
    flag () {
      if (this.value.length == 3) {
        return true
      } else {
        return false
      }
    }
  }
}
</script>
<style lang="less" scoped>
.add-container {
  height: 100%;
  .box-card {
    /deep/.el-tabs--left {
      height: 100% !important;
      //富文本编辑器
      //   /deep/.el-tabs__content {
      //     height: 100% !important;
      //     .el-tab-pane {
      //       height: 100%;
      //     }
      //   }
    }
    .el-card__body {
      height: 100%;
      .el-form {
        height: 100%;
      }
    }
    height: 100%;
    .el-alert {
      margin-bottom: 20px;
    }
    .el-steps {
      margin: 30px 100px;
    }
    /deep/.el-form-item__content {
      margin-left: 0px !important;
    }

    /deep/.ql-editor {
      height: 400px;
    }
    .el-button {
      margin-top: 20px;
    }
  }
}
</style>
